"use scrict";
import { COLOR_PRIMARY  } from "@/config/const.js";
	
export default {
	data() {
		return {
			COLOR_PRIMARY
		}
	},
	model: {
		prop: "index",
		event: "change"
	},
	props: {
		menus: {
			type: Array,
			default: []
		},
		index: {
			type: Number,
			default: 0
		}
	},
	methods: {
		change: function (index) {
			this.$emit("change", index);
		}
	}
}
